<template>
    <div>
        <div class="home" style="background-color: rgba(0, 0, 0, 0.03);">
            <Navbar />
            
            <div class="container">
                <div class="row mt-4">
                    <b-col md="12">
                        <b-card
                            overlay
                            img-src="https://picsum.photos/900/250/?image=3"
                            img-alt="Card Image"
                            text-variant="white"
                            title="Your Profile"
                        >
                        </b-card>
                    </b-col>
                </div>
                <div class="row mt-4">
                    <b-col md="8">
                        <div class="mb-4">
                            <div class="row">
                                <b-col md="12">
                                    <b-card>
                                        <b-form @submit="onSubmit">
                                            <b-form-group
                                                id="input-group-1"
                                                label="Your Name:"
                                                label-for="input-1"
                                            >
                                                <b-form-input
                                                    id="input-1"
                                                    v-model="user.name"
                                                    placeholder="Enter name"
                                                    required
                                                ></b-form-input>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-2"
                                                label="Telephone Number:"
                                                label-for="input-2"
                                            >
                                                <b-form-input
                                                    id="input-2"
                                                    v-model="user.telephone"
                                                    placeholder="Enter telephone"
                                                    required
                                                ></b-form-input>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-3"
                                                label="Email address:"
                                                label-for="input-3"
                                            >
                                                <b-form-input
                                                    id="input-3"
                                                    v-model="user.email"
                                                    type="email"
                                                    placeholder="Enter email"
                                                    required
                                                ></b-form-input>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-5"
                                                label="Country:"
                                                label-for="input-5"
                                            >
                                                <b-form-select v-model="user.country" :options="countries" size="sm" class="mt-3"></b-form-select>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-6"
                                                label="Province:"
                                                label-for="input-6"
                                            >
                                                <b-form-select v-model="user.province" :options="provinces" size="sm" class="mt-3" v-on:change="getCity"></b-form-select>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-7"
                                                label="City:"
                                                label-for="input-7"
                                            >
                                                <b-form-select v-model="user.city" :options="cities" size="sm" class="mt-3" v-on:change="getVillage"></b-form-select>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-8"
                                                label="Village:"
                                                label-for="input-8"
                                            >
                                                <b-form-select v-model="user.village" :options="villages" size="sm" class="mt-3"></b-form-select>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-9"
                                                label="Street:"
                                                label-for="input-9"
                                            >
                                                <b-form-input
                                                    id="input-9"
                                                    v-model="user.street"
                                                    placeholder="Street your home"
                                                    required
                                                ></b-form-input>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-10"
                                                label="Weight:"
                                                label-for="input-10"
                                            >
                                                <b-form-input
                                                    id="input-10"
                                                    v-model="user.weight"
                                                    placeholder="Weight (kg)"
                                                    type="number"
                                                    required
                                                ></b-form-input>
                                            </b-form-group>

                                            <b-form-group
                                                id="input-group-11"
                                                label="Height:"
                                                label-for="input-11"
                                            >
                                                <b-form-input
                                                    id="input-11"
                                                    v-model="user.height"
                                                    placeholder="Height (cm)"
                                                    type="number"
                                                    required
                                                ></b-form-input>
                                            </b-form-group>

                                            <b-form-group label="History Disease:" v-slot="{ ariaDescribedby }">
                                                <b-form-checkbox-group
                                                    id="checkbox-group-1"
                                                    v-model="user.diseases"
                                                    :options="diseases"
                                                    :aria-describedby="ariaDescribedby"
                                                    name="diseases"
                                                >
                                                </b-form-checkbox-group>
                                            </b-form-group>

                                            <b-form-group label="Are You snoring?" v-slot="{ ariaDescribedby }">
                                                <b-form-radio v-model="user.snore" :aria-describedby="ariaDescribedby" name="snore" value="Y">Yes</b-form-radio>
                                                <b-form-radio v-model="user.snore" :aria-describedby="ariaDescribedby" name="snore" value="N">No</b-form-radio>
                                            </b-form-group>

                                            <b-button type="submit" variant="success">Save</b-button>
                                        </b-form>
                                    </b-card>
                                </b-col>
                            </div>
                        </div>
                    </b-col>
                    <b-col md="4">
                        <div class="mb-4">
                            <b-card class="justify-content-md-center">
                                <b-avatar variant="info" src="https://demos.creative-tim.com/vue-material-dashboard-pro/img/faces/marc.jpg" size="10rem" class="mx-auto d-block" style="margin-top: -100px"></b-avatar>
                                <div class="mt-3">
                                    <h5 class="text-center">{{ user.name }}</h5>
                                    <p class="text-center">{{ user.telephone }}</p>
                                    <p class="text-center">{{ user.email }}</p>
                                    <p class="text-center">{{ user.street }}, {{ user.village }}, {{ user.city }} <br>
                                        {{ user.province }}, {{ user.country }}
                                    </p>
                                    <p class="text-center">{{ user.height }} cm / {{ user.weight }} kg </p>
                                    <p class="text-center">{{ user.disease }}</p>
                                    
                                    <div class="text-center" v-if="user.snore == 'Y'">
                                        Snoring
                                    </div>
                                    
                                </div>
                                
                            </b-card>
                        </div>
                    </b-col>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Navbar from "@/components/Navbar.vue";
    import axios from 'axios';
    
    axios.defaults.withCredentials = true;
    axios.defaults.headers.common = {
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json'
    };

    export default {
        name: "Profile",
        components: {
            Navbar,
        },
        data() {
            return {
                loggedIn: localStorage.getItem('loggedIn'),
                token: localStorage.getItem('token'),
                user: [],
                loginFailed: null,
                message: '',
                countries: [
                    { value: 'INDONESIA', text: 'INDONESIA' }
                ],
                diseases: [
                    { text: 'ASTHMA', value: 'ASTHMA' },
                    { text: 'TBC', value: 'TBC' },
                    { text: 'COVID-19', value: 'COVID-19' }
                ],
                provinces: [],
                cities: [],
                villages: []
            }
        },
        mounted: function () {
            if (!this.loggedIn) {
                return this.$router.push({ name: 'Home' })
            }
        },
        created() {
            this.getProfile();
            this.getProvince();
        },
        methods: {
            getProfile: function(){
                let currentObj = this;
                let config = {
                    headers: {
                        'Authorization': 'Bearer ' + currentObj.token
                    },
                }
                axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                .then(response => {
                    //debug cookie
                    console.log(response)

                    axios.post("https://api.oxion.tecorp.me/api/profile", config)
                    .then(function(response){
                        //console.log(response.data);
                        if(response == 'Unauthenticated'){

                            localStorage.removeItem("loggedIn")    
                            localStorage.removeItem('token')
                            return this.$router.push({ name: 'Home' })

                        } else if (response.data.error) {
                            currentObj.message = response.data.message;
                            //remove localStorage
                            localStorage.removeItem("loggedIn")    
                            localStorage.removeItem('token')
                            return this.$router.push({ name: 'Home' })
                        }else{
                            currentObj.user = response.data.data;
                            
                            currentObj.cities = [];
                            var cities = '';
                            
                            axios.get('https://api.oxion.tecorp.me/api/regencies/'+currentObj.user.province)
                            .then(response => {
                                cities = response.data.data;
                                for(var i = 0; i < cities.length; i++) {
                                    currentObj.cities.push({value: cities[i].name, text: cities[i].name});
                                }
                            })

                            currentObj.villages = [];
                            var villages = '';
                            
                            axios.get('https://api.oxion.tecorp.me/api/districts/'+currentObj.user.city)
                            .then(response => {
                                villages = response.data.data;
                                for(var i = 0; i < villages.length; i++) {
                                    currentObj.villages.push({value: villages[i].name, text: villages[i].name});
                                }
                            })
                        }
                    }).catch(error => {
                        console.log(error)
                        localStorage.removeItem("loggedIn")    
                        localStorage.removeItem('token')
                        return this.$router.push({ name: 'Login' })
                    })
                })
            },
            getProvince: function(){
                let currentObj = this;
                var provinces = '';
                
                axios.get('https://api.oxion.tecorp.me/api/provinces')
                .then(response => {
                    provinces = response.data.data;
                    for(var i = 0; i < provinces.length; i++) {
                        currentObj.provinces.push({value: provinces[i].name, text: provinces[i].name});
                    }
                })
            },
            getCity: function(){
                let currentObj = this;
                currentObj.cities = [];
                var cities = '';
                
                axios.get('https://api.oxion.tecorp.me/api/regencies/'+currentObj.user.province)
                .then(response => {
                    cities = response.data.data;
                    console.log(cities)
                    for(var i = 0; i < cities.length; i++) {
                        currentObj.cities.push({value: cities[i].name, text: cities[i].name});
                    }
                })
            },
            getVillage: function(){
                let currentObj = this;
                currentObj.villages = [];
                var villages = '';
                
                axios.get('https://api.oxion.tecorp.me/api/districts/'+currentObj.user.city)
                .then(response => {
                    villages = response.data.data;
                    console.log(villages)
                    for(var i = 0; i < villages.length; i++) {
                        currentObj.villages.push({value: villages[i].name, text: villages[i].name});
                    }
                })
            },
            onSubmit(event) {
                event.preventDefault()
                alert(JSON.stringify(this.user))

            },
        }
    }
</script>



