<template>
    <div>
        <div class="home" style="background-color: rgba(0, 0, 0, 0.03);">
            <Navbar />
            <div class="container">
                <b-alert v-if="loginFailed" variant="danger" show>{{ message }}</b-alert>
                <b-row class="justify-content-md-center">
                    <b-col cols="12" md="6">
                        <b-card class="mt-3">
                            <b-card-body>
                                <b-form @submit="onSubmit" @reset="onReset" v-if="show">
                                    <h3 class="text-center">Register</h3>
                                    <b-form-group
                                        id="input-group-1"
                                        label="Your Name:"
                                        label-for="input-1"
                                    >
                                        <b-form-input
                                            id="input-1"
                                            v-model="form.name"
                                            placeholder="Enter name"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-2"
                                        label="Telephone Number:"
                                        label-for="input-2"
                                    >
                                        <b-form-input
                                            id="input-2"
                                            v-model="form.telephone"
                                            placeholder="Enter telephone"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-3"
                                        label="Email address:"
                                        label-for="input-3"
                                    >
                                        <b-form-input
                                            id="input-3"
                                            v-model="form.email"
                                            type="email"
                                            placeholder="Enter email"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group 
                                        id="input-group-4" 
                                        label="Password" 
                                        label-for="input-4"
                                    >
                                        <b-form-input
                                            id="input-4"
                                            type="password"
                                            v-model="form.password"
                                            placeholder="Enter password"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-5"
                                        label="Country:"
                                        label-for="input-5"
                                    >
                                        <b-form-select v-model="countries_selected" :options="countries" size="sm" class="mt-3"></b-form-select>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-6"
                                        label="Province:"
                                        label-for="input-6"
                                    >
                                        <b-form-select v-model="provinces_selected" :options="provinces" size="sm" class="mt-3" v-on:change="getCity"></b-form-select>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-7"
                                        label="City:"
                                        label-for="input-7"
                                    >
                                        <b-form-select v-model="cities_selected" :options="cities" size="sm" class="mt-3" v-on:change="getVillage"></b-form-select>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-8"
                                        label="Village:"
                                        label-for="input-8"
                                    >
                                        <b-form-select v-model="villages_selected" :options="villages" size="sm" class="mt-3"></b-form-select>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-9"
                                        label="Street:"
                                        label-for="input-9"
                                    >
                                        <b-form-input
                                            id="input-9"
                                            v-model="form.street"
                                            placeholder="Street your home"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-10"
                                        label="Weight:"
                                        label-for="input-10"
                                    >
                                        <b-form-input
                                            id="input-10"
                                            v-model="form.weight"
                                            placeholder="Weight (kg)"
                                            type="number"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group
                                        id="input-group-11"
                                        label="Height:"
                                        label-for="input-11"
                                    >
                                        <b-form-input
                                            id="input-11"
                                            v-model="form.height"
                                            placeholder="Height (cm)"
                                            type="number"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group label="History Disease:" v-slot="{ ariaDescribedby }">
                                        <b-form-checkbox-group
                                            id="checkbox-group-1"
                                            v-model="diseases_selected"
                                            :options="diseases"
                                            :aria-describedby="ariaDescribedby"
                                            name="diseases"
                                        >
                                        </b-form-checkbox-group>
                                    </b-form-group>

                                    <b-form-group label="Are You snoring?" v-slot="{ ariaDescribedby }">
                                        <b-form-radio v-model="form.snore" :aria-describedby="ariaDescribedby" name="snore" value="Y">Yes</b-form-radio>
                                        <b-form-radio v-model="form.snore" :aria-describedby="ariaDescribedby" name="snore" value="N">No</b-form-radio>
                                    </b-form-group>

                                    <p>Already <router-link to="/login">Account</router-link> ?</p>

                                    <b-button type="submit" variant="success">Submit</b-button>
                                    <b-button type="reset" variant="danger">Reset</b-button>
                                </b-form>
                            </b-card-body>
                        </b-card>
                    </b-col>
                </b-row>
                
                <b-card class="mt-3" header="Form Data Result">
                <pre class="m-0">{{ form }}</pre>
                </b-card>
            </div>
        </div>
    </div>
</template>

<script>
    import Navbar from "@/components/Navbar.vue";
    import axios from 'axios';

    export default {
        name: "Register",
        components: {
            Navbar,
        },
        data() {
            return {
                form: {
                    name: '',
                    email: '',
                    password: '',
                    telephone: '',
                    street: '',
                    weight: '',
                    height: '',
                    snore: '',
                },
                message: '',

                countries_selected: null,
                provinces_selected: null,
                cities_selected: null,
                villages_selected: null,

                countries: [
                    { value: 'INDONESIA', text: 'INDONESIA' }
                ],
                provinces: [],
                cities: [],
                villages: [],

                diseases_selected: [],
                diseases: [
                    { text: 'ASTHMA', value: 'ASTHMA' },
                    { text: 'TBC', value: 'TBC' },
                    { text: 'COVID-19', value: 'COVID-19' }
                ],
                
                show: true,
                //state loggedIn with localStorage
                loggedIn: localStorage.getItem('loggedIn'),
                //state token
                token: localStorage.getItem('token'),
                //state user
                user: [],
                //state validation
                validation: [],
                //state login failed
                loginFailed: null
            }
        },
        methods: {
            getProvince: function(){
                let currentObj = this;
                var provinces = '';
                
                axios.get('https://api.oxion.tecorp.me/api/provinces')
                .then(response => {
                    provinces = response.data.data;
                    for(var i = 0; i < provinces.length; i++) {
                        currentObj.provinces.push({value: provinces[i].name, text: provinces[i].name});
                    }
                })
            },
            getCity: function(){
                let currentObj = this;
                currentObj.cities = [];
                var cities = '';
                
                axios.get('https://api.oxion.tecorp.me/api/regencies/'+this.provinces_selected)
                .then(response => {
                    cities = response.data.data;
                    console.log(cities)
                    for(var i = 0; i < cities.length; i++) {
                        currentObj.cities.push({value: cities[i].name, text: cities[i].name});
                    }
                })
            },
            getVillage: function(){
                let currentObj = this;
                currentObj.villages = [];
                var villages = '';
                
                axios.get('https://api.oxion.tecorp.me/api/districts/'+this.cities_selected)
                .then(response => {
                    villages = response.data.data;
                    console.log(villages)
                    for(var i = 0; i < villages.length; i++) {
                        currentObj.villages.push({value: villages[i].name, text: villages[i].name});
                    }
                })
            },
            onSubmit(event) {
                event.preventDefault()
                //alert(JSON.stringify(this.form))

                if (this.form.name && this.form.email && this.form.password && this.form.telephone && this.countries_selected 
                    && this.provinces_selected && this.cities_selected && this.villages_selected && this.form.street 
                    && this.form.weight && this.form.height && this.diseases_selected && this.form.snore) {
                    axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                    .then(response => {

                        //debug cookie
                        console.log(response)

                        axios.post('https://api.oxion.tecorp.me/api/register', {
                            name: this.form.name,
                            email: this.form.email,
                            password: this.form.password,
                            telephone: this.form.telephone,
                            country: this.countries_selected,
                            province: this.provinces_selected,
                            city: this.cities_selected,
                            village: this.villages_selected,
                            street: this.form.street,
                            weight: this.form.weight,
                            height: this.form.height,
                            disease: this.diseases_selected,
                            snore: this.form.snore,
                        }).then(res => {

                            //debug user register
                            console.log(res)

                            if (res.data.error) {

                                //set state login failed
                                this.loginFailed = true

                                this.message = res.data.message

                            } else {

                                //remove localStorage
                                localStorage.removeItem("loggedIn")    

                                localStorage.removeItem('token')

                                //set localStorage
                                localStorage.setItem("loggedIn", "true")

                                //set localStorage Token
                                localStorage.setItem("token", res.data.access_token)

                                //change state
                                this.loggedIn = true

                                this.message = res.data.message

                                

                                
                                axios.post('https://api.oxion.tecorp.me/api/userotp', {
                                    user_id: res.data.data.user_id
                                }).then(res => {
                                    //debug user otp
                                    console.log(res)
                                    if (res.data.error) {

                                        //set state login failed
                                        this.loginFailed = true
                                        this.message = res.data.message
                                    } else {
                                        return this.$router.push({ name: 'OTP' })
                                    }
                                }).catch(error => {
                                    console.log(error)
                                })
                            }

                        }).catch(error => {
                            console.log(error)
                        })

                    })
                }

                this.validation = []

                if (!this.form.email) {
                    this.validation.email = true
                }

                if (!this.form.password) {
                    this.validation.password = true
                }

            },
            onReset(event) {
                event.preventDefault()
                // Reset our form values
                this.form.name = ''
                this.form.email = ''
                this.form.password = ''
                
                // Trick to reset/clear native browser form validation state
                this.show = false
                this.$nextTick(() => {
                this.show = true
                })
            }
        },
        //check user already logged in
        mounted() {
            if (this.loggedIn) {
                return this.$router.push({ name: 'Profile' })
            }

            this.getProvince();
        }
    }
</script>


