<template>
    <div>
        <div class="home" style="background-color: rgba(0, 0, 0, 0.03);">
            <Navbar />
            
            <div class="container">
                <div class="row mt-4">
                    <b-col md="12">
                        <b-card no-body class="text-center card-rounded-5">
                            <div class="bg-main text-light card-rounded-5">
                                <div class="row mt-2">
                                    <b-col md="12">
                                        <span style="font-weight: bold; font-size: 20px">Your Inpoin</span>
                                    </b-col>
                                </div>
                                <div class="row mt-2">
                                    <b-col md="12">
                                        <b-avatar variant="info" src="https://placekitten.com/300/300" size="6rem"></b-avatar>
                                    </b-col>
                                </div>
                                <div class="row mt-2">
                                    <b-col md="12">
                                        <span style="font-weight: bold; font-size: 20px">{{ inpoinProfile.name }}</span>
                                    </b-col>
                                </div>
                                <div class="row mb-2">
                                    <b-col md="12">
                                        <b-avatar size="sm" variant="light" src="https://image.flaticon.com/icons/png/512/1251/1251128.png"></b-avatar>
                                        <span style="font-weight: lighter;">Master {{ inpoinProfile.title }}</span>
                                    </b-col>
                                </div>
                            </div>
                        </b-card>
                    </b-col>
                </div>

                <div class="row mt-4">
                    <b-col md="12">
                        <b-card no-body class="text-center card-rounded-5">
                            <b-row align-v="stretch" class="mt-2 ml-2 mr-2">
                                <b-col>
                                    <div>
                                        <span style="font-weight: bold; font-size: 30px">{{ inpoinProfile.exp }}</span>
                                        <br>
                                        <span style="font-weight: bold;">XP</span>
                                    </div>
                                </b-col>
                                <b-col>
                                    <div>
                                        <span style="font-weight: bold; font-size: 30px">{{ inpoinProfile.level }}</span>
                                        <br>
                                        <span style="font-weight: bold;">Level</span>
                                    </div>
                                </b-col>
                                <b-col>
                                    <div>
                                        <span style="font-weight: bold; font-size: 30px">{{ inpoinProfile.coin }}</span>
                                        <br>
                                        <span style="font-weight: bold;">Coin</span>
                                    </div>
                                </b-col>
                            </b-row>

                            <hr class="ml-2 mr-2">

                            <b-row class="ml-2 mr-2 mb-2">
                                <b-col>
                                    <span style="font-weight: lighter; font-size: 24px;">Your badges</span>
                                </b-col>
                            </b-row>

                            <b-row align-v="stretch" class="mt-2 mb-2">
                                <b-col>
                                    <div>
                                        <b-avatar variant="light" src="https://image.flaticon.com/icons/png/512/1243/1243961.png"></b-avatar>
                                        <br>
                                        <span>Medal</span>
                                    </div>
                                </b-col>
                                <b-col>
                                    <div>
                                        <b-avatar variant="light" src="https://image.flaticon.com/icons/png/512/1200/1200705.png"></b-avatar>
                                        <br>
                                        <span>Badge</span>
                                    </div>
                                </b-col>
                                <b-col>
                                    <div>
                                        <b-avatar variant="light" src="https://image.flaticon.com/icons/png/512/1200/1200728.png"></b-avatar>
                                        <br>
                                        <span>World</span>
                                    </div>
                                </b-col>
                                <b-col>
                                    <div>
                                        <b-avatar variant="light" src="https://image.flaticon.com/icons/png/512/1255/1255076.png"></b-avatar>
                                        <br>
                                        <span>Health</span>
                                    </div>
                                </b-col>
                            </b-row>

                            <hr class="ml-2 mr-2">

                            <div class="accordion ml-2 mr-2" role="tablist">
                                <b-row align-v="stretch" class="mt-2 mb-2">
                                    <b-col>
                                        <b-button pill block v-b-toggle.target variant="outline-success">Target Today</b-button>
                                    </b-col>
                                    <b-col>
                                        <b-button pill block v-b-toggle.mission variant="outline-success">Mission</b-button>
                                    </b-col>
                                </b-row>

                                <b-card no-body class="mb-1">
                                    <b-collapse id="target" visible accordion="my-accordion" role="tabpanel">
                                        <b-card-body>
                                            <b-card-text>
                                                <b-table :items="inpoinProfileActivity" :fields="fields" striped responsive="sm" sticky-header head-variant="dark">
                                                    
                                                </b-table>
                                            </b-card-text>
                                        </b-card-body>
                                    </b-collapse>
                                </b-card>

                                <b-card no-body class="mb-1">
                                    <b-collapse id="mission" accordion="my-accordion" role="tabpanel">
                                        <b-card-body>
                                            <b-card-text>{{ text }}</b-card-text>
                                        </b-card-body>
                                    </b-collapse>
                                </b-card>

                            </div>
                        </b-card>
                    </b-col>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import Navbar from "@/components/Navbar.vue";
    import axios from 'axios';
    
    axios.defaults.withCredentials = true;
    axios.defaults.headers.common = {
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json'
    };

    export default {
        name: "Inpoin",
        components: {
            Navbar,
        },
        data() {
            return {
                loggedIn: localStorage.getItem('loggedIn'),
                token: localStorage.getItem('token'),
                inpoinProfile: [],
                fields: ['origin', 'trxAmount', 'type'],
                inpoinProfileActivity: [],
                loginFailed: null,
                message: '',
                getsecret: '',
                access_token: '',
                text: 'No Mission today'
            }
        },
        mounted: function () {
            if (!this.loggedIn) {
                return this.$router.push({ name: 'Home' })
            }

            this.getInpoin();
        },
        methods: {
            getInpoin: function(){
                let currentObj = this;
                axios.get('https://api.oxion.tecorp.me/api/inpoin/getsecret')
                .then(response => {
                    console.log(response)
                    currentObj.getsecret = response.data.data.data.secret
                    axios.post('https://api.oxion.tecorp.me/api/inpoin/login', {
                        getsecret: currentObj.getsecret
                    })
                    .then(response => {
                        console.log(response)
                        currentObj.access_token = response.data.data.data.access_token

                        axios.post('https://api.oxion.tecorp.me/api/inpoin/profile', {
                            access_token: currentObj.access_token
                        })
                        .then(response => {
                            console.log(response)
                            currentObj.inpoinProfile = response.data.data.data.profiles
                        }).catch(error => {
                            console.log(error)
                        })

                        axios.post('https://api.oxion.tecorp.me/api/inpoin/profile/activity', {
                            access_token: currentObj.access_token
                        })
                        .then(response => {
                            console.log(response)
                            currentObj.inpoinProfileActivity = response.data.data.data
                        }).catch(error => {
                            console.log(error)
                        })
                    }).catch(error => {
                        console.log(error)
                    })
                }).catch(error => {
                    console.log(error)
                })
            }
        }
    }
</script>

<style>
    
</style>



