<template>
  <div>
    <canvas id="card-chart"></canvas>

    <b-card class="mt-3" header="Form Data Result">
        <pre class="m-0">{{ project_tool }}</pre>
    </b-card>

    <b-card class="mt-3" header="Form Data Result">
        <pre class="m-0">{{ project_tool_data }}</pre>
    </b-card>

    <b-card class="mt-3" header="Form Data Result">
        <pre class="m-0">{{ project_param }}</pre>
    </b-card>
  </div>
</template>

<script>
import axios from 'axios';
import Chart from 'chart.js'
import ChartDataLabels from 'chartjs-plugin-datalabels'
import ChartStreaming from 'chartjs-plugin-streaming';
Chart.plugins.register(ChartDataLabels)
Chart.plugins.register(ChartStreaming)

export default {
    name: 'CardChart',
    data() {
        return {
            project_param: [],
            project_tool: [],
            project_tool_data: [],
            loggedIn: localStorage.getItem('loggedIn'),
            token: localStorage.getItem('token'),
            loginFailed: null,
            message: '',

            labelsChart: '',
            datasetsDataChart: '',

            cardChart: {
                type: "line",
                data: {
                    labels: [],
                    datasets: [
                        {
                            label: '',
                            data: [],
                            backgroundColor: "rgba(54,73,93,.5)",
                            borderColor: "#36495d",
                            borderWidth: 3
                        }
                    ]
                },
                plugins: [ChartDataLabels],
                options: {
                    responsive: true,
                    plugins: {
                        datalabels: {
                            color: 'blue',
                            labels: {
                                title: {
                                    font: {
                                        weight: 'bold'
                                    }
                                },
                                value: {
                                    color: 'green'
                                }
                            }
                        }
                    },
                    lineTension: 1,
                    scales: {
                        yAxes: [
                            {
                                ticks: {
                                    beginAtZero: true,
                                    padding: 25
                                }
                            }
                        ]
                    }
                }
            }
        }
    },
    mounted() {
        this.getProjectTool();
        this.getProjectParam();
        //const ctx = document.getElementById('card-chart');
        //new Chart(ctx, this.cardChart);
    },
    methods: {
        onRefresh: function(chart){
            var now = Date.now();
            chart.data.datasets.forEach(function(dataset) {
                dataset.data.push({
                    x: now,
                    y: (Math.random() > 0.5 ? 1.0 : -1.0) * Math.round(Math.random() * 100)
                });
            });
        },
        getChart: function(labelsChart,datasetsDataChart){
            const ctx = document.getElementById('card-chart');
            new Chart(ctx, {
                type: "line",
                data: {
                    labels: labelsChart,
                    datasets: [
                        {
                            label: this.project_param,
                            data: datasetsDataChart,
                            backgroundColor: "rgba(54,73,93,.5)",
                            borderColor: "#36495d",
                            borderWidth: 3
                        }
                    ]
                },
                plugins: [ChartDataLabels],
                options: {
                    responsive: true,
                    plugins: {
                        datalabels: {
                            color: 'blue',
                            labels: {
                                title: {
                                    font: {
                                        weight: 'bold'
                                    }
                                },
                                value: {
                                    color: 'green'
                                }
                            }
                        }
                    },
                    lineTension: 1,
                    scales: {
                        xAxes: [
                            {
                                realtime: {
                                    onRefresh: function(chart) {
                                        chart.data.datasets.forEach(function(dataset) {
                                            dataset.data.push({
                                                x: '1',
                                                y: '100'
                                            });
                                        });
                                    },
                                    duration: 20000,
                                    refresh: 1000,
                                    delay: 2000
                                }
                            }
                        ],
                        yAxes: [
                            {
                                ticks: {
                                    beginAtZero: true,
                                    padding: 25
                                }
                            }
                        ]
                    }
                }
            });
        },
        getProjectTool: function(){
            let currentObj = this;
            let config = {
                headers: {
                    'Authorization': 'Bearer ' + currentObj.token
                },
            }
            axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
            .then(response => {
                //debug cookie
                console.log(response)

                axios.get("https://api.oxion.tecorp.me/api/project/tool/E95Af293d9", config)
                .then(function(response){
                    //console.log(response.data);
                    if(response == 'Unauthenticated'){

                        localStorage.removeItem("loggedIn")    
                        localStorage.removeItem('token')
                        return this.$router.push({ name: 'Home' })

                    } else if (response.data.error) {
                        currentObj.message = response.data.message;
                        //remove localStorage
                        localStorage.removeItem("loggedIn")    
                        localStorage.removeItem('token')
                        return this.$router.push({ name: 'Home' })
                    }else{
                        console.log(response.data.data)
                        currentObj.project_tool = response.data.data;
                        var labelsChart = currentObj.cardChart.data.labels;
                        var datasetsDataChart = currentObj.cardChart.data.datasets[0].data;
                        for ( var i = 0; i < response.data.data.length; i++ ) {
                            // Then push our data to the new arrays
                            labelsChart.push(response.data.data[i] ? response.data.data[i].created_tm : '');
                            datasetsDataChart.push(response.data.data[i] ? JSON.parse(response.data.data[i].data).Oksigen : '');
                        }
                        currentObj.project_tool_data = JSON.parse(response.data.data[0].data);
                        currentObj.getChart(labelsChart,datasetsDataChart)
                    }
                }).catch(error => {
                    console.log(error)
                    //localStorage.removeItem("loggedIn")    
                    //localStorage.removeItem('token')
                    //return this.$router.push({ name: 'Login' })
                })
            })
        },
        getProjectParam: function(){
            let currentObj = this;
            let config = {
                headers: {
                    'Authorization': 'Bearer ' + currentObj.token
                },
            }

            axios.get("https://api.oxion.tecorp.me/api/project/param/E95Af293d9", config)
            .then(function(response){
                //console.log(response.data);
                if(response == 'Unauthenticated'){

                    localStorage.removeItem("loggedIn")    
                    localStorage.removeItem('token')
                    return this.$router.push({ name: 'Home' })

                } else if (response.data.error) {
                    currentObj.message = response.data.message;
                    //remove localStorage
                    localStorage.removeItem("loggedIn")    
                    localStorage.removeItem('token')
                    return this.$router.push({ name: 'Home' })
                }else{
                    //console.log(response.data.data)
                    currentObj.cardChart.data.datasets[0].label = response.data.data[0].name;
                    currentObj.project_param = response.data.data[0].name;
                }
            }).catch(error => {
                console.log(error)
                localStorage.removeItem("loggedIn")    
                localStorage.removeItem('token')
                return this.$router.push({ name: 'Login' })
            })
        }
    }
}
</script>


