<template>
    <div>
      <b-navbar toggleable="lg" type="dark" class="bg-main">
        <div class="container">
          <b-navbar-brand href="#">
            <router-link to="/register"><b-icon-arrow-left variant="light"></b-icon-arrow-left></router-link>
            OXION+
            </b-navbar-brand>
        </div>
      </b-navbar>
        <div class="container">
            <div class="mt-3">
                  <b-alert v-if="otpFailed" variant="danger" show>{{ message }}</b-alert>
                  <div class="mc-10" style="display: flex; flex-direction: row;">
                      <h3 class="text-center mt-200px" style="color: white">Your OTP Code</h3>
                  </div>
                  <div class="mc-50" style="display: flex; flex-direction: row;">
                      <v-otp-input
                      ref="otpInput"
                      input-classes="otp-input"
                      separator="-"
                      :num-inputs="6"
                      :should-auto-focus="true"
                      :is-input-num="true"
                      @on-change="handleOnChange"
                      @on-complete="handleOnComplete"
                      />
                  </div>
                  <div class="mc-13" style="display: flex; flex-direction: row;">
                    <p class="text-center mb-200px">Resend OTP Code</p>
                  </div>
                
            </div>
        </div>
      
    </div>
  
</template>

<script>
import axios from 'axios';

export default {
  name: 'OTP',
  data() {
      return {
          message: '',
          //state login failed
          otpFailed: null
      }
  },
  methods: {
    handleOnComplete(value) {
      console.log('OTP completed: ', value);

      axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
      .then(response => {

          //debug cookie
          console.log(response)

          axios.post('https://api.oxion.tecorp.me/api/userotp/'+value, {
              
          }).then(res => {
              //debug user otp
              console.log(res)
              if (res.data.error) {

                  //set state login failed
                  this.otpFailed = true
                  this.message = res.data.message
              } else {
                  //remove localStorage
                  localStorage.removeItem("loggedIn")    
                  localStorage.removeItem('token')

                  //set localStorage
                  localStorage.setItem("loggedIn", "true")
                  localStorage.setItem("token", res.data.access_token)

                  return this.$router.push({ name: 'Profile' })
              }
          }).catch(error => {
              console.log(error)
          })
      })
    },
    handleOnChange(value) {
      console.log('OTP changed: ', value);
    },
  },
};
</script>

<style>
  
  .otp-input {
    width: 100%;
    height: 25%;
    padding: 5px;
    margin: 0 3px;
    font-size: 25px;
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    text-align: center;
  }
  .otp-input::-webkit-inner-spin-button,
  .otp-input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .mt-200px {
    width: 100%;
    height: 100%;
    margin: 100px auto 0 auto;
  }

  .mb-200px {
    width: 100%;
    height: 100%;
    margin: -100px auto 150px auto;
  }

  @media only screen and (max-width: 600px) {
    .otp-input {
      height: 12%;
    }
  }

  .mc-50 {
    width: 100%;
    height: 50vh;
    margin: 0 auto;
  }

  .mc-10 {
    width: 100%;
    height: 10vh;
    margin: 0 auto;
  }

  .mc-13 {
    width: 100%;
    height: 13vh;
    margin: 0 auto;
  }
</style>


