import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/Home'
import Login from '@/views/Login'
import Register from '@/views/Register'
import ForgotPassword from '@/views/ForgotPassword'
import Profile from '@/views/Profile'
import OTP from '@/views/OTP'
import Project from '@/views/Project'
import Dashboard from '@/views/Dashboard'
import Inpoin from '@/views/Inpoin'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      meta: {
        hideBackgroundImage: true
      },
      component: Home
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/forgot-password',
      name: 'ForgotPassword',
      component: ForgotPassword
    },
    {
      path: '/otp',
      name: 'OTP',
      component: OTP
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      meta: {
        hideBackgroundImage: true
      },
      component: Dashboard
    },
    {
      path: '/profile',
      name: 'Profile',
      meta: {
        hideBackgroundImage: true
      },
      component: Profile
    },
    {
      path: '/project',
      name: 'Project',
      meta: {
        hideBackgroundImage: true
      },
      component: Project
    },
    {
      path: '/inpoin',
      name: 'Inpoin',
      meta: {
        hideBackgroundImage: true
      },
      component: Inpoin
    }
  ]
})



