<template>
  <div>
    <b-navbar toggleable="lg" type="dark" class="bg-main">
      <div class="container">
        <b-navbar-brand href="#"><img src="../assets/images/x_white.png" alt="OXION+" width="60px"></b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

        <b-collapse id="nav-collapse" is-nav v-if="isLoggedIn">
          <b-navbar-nav>
            
            <li class="nav-item">
              <router-link class="nav-link" to="/dashboard">Dashboard</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/project">Project</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/inpoin">Inpoin</router-link>
            </li>
          </b-navbar-nav>

          <b-navbar-nav class="ml-auto">
            <li class="nav-item">
              <router-link class="nav-link" to="/profile">Profile</router-link>
            </li>
            <li class="nav-item" @click="logout" style="cursor:pointer">
              <router-link class="nav-link" to="">Logout</router-link>
            </li>
          </b-navbar-nav>
        </b-collapse>

        <b-collapse id="nav-collapse" is-nav v-else>
          <b-navbar-nav>
            
            <li class="nav-item">
              <router-link class="nav-link" to="/">Home</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/tipsntrick">Tips & Trick</router-link>
            </li>
          </b-navbar-nav>

          <b-navbar-nav class="ml-auto">
            <li class="nav-item">
              <router-link class="nav-link" to="/login">Login</router-link>
            </li>
            <li class="nav-item">
              <router-link class="nav-link" to="/register">Register</router-link>
            </li>
          </b-navbar-nav>
        </b-collapse>

      </div>
    </b-navbar>
  </div>
</template>

<script>
    import axios from 'axios';

    axios.defaults.withCredentials = true;
    axios.defaults.headers.common = {
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json'
    };

    export default {
      name: "Navbar",
      data() {
        return {
          isLoggedIn: false,
          token: localStorage.getItem('token')
        }
      },
      mounted: function () {
        if(localStorage.getItem('loggedIn')) {
          this.isLoggedIn = true;
        } else {
          this.isLoggedIn = false;
        }
      },
      methods: {
        logout() {
          axios.post('https://api.oxion.tecorp.me/api/logout')
          .then(() => {
              //remove localStorage
              localStorage.removeItem("loggedIn")    

              localStorage.removeItem('token')

              //redirect
              return this.$router.push({ name: 'Home' })
          })
        }
      },
    };
</script>

 <style>
    .navbar-dark .navbar-nav .nav-link {
      color: #fff;
    }
 </style>



