<template>
    <div>
        <div class="home" style="background-color: rgba(0, 0, 0, 0.03);">
            <Navbar />
            
            <div class="container">
                <div class="row mt-4">
                    <b-col md="12">
                        <b-card
                            overlay
                            img-src="https://picsum.photos/900/250/?image=3"
                            img-alt="Card Image"
                            text-variant="white"
                            title="Your Project"
                        >
                        </b-card>
                    </b-col>
                </div>

                <div class="row mt-4">
                    <b-col md="2" class="ml-auto p-3">
                        <b-button v-b-modal.modal-create block size="md" variant="info" class="ml-auto">
                            <b-icon icon="plus-circle-fill"></b-icon> &nbsp;Add Project
                        </b-button>
                    </b-col>
                </div>


                <div class="row">
                    <b-col md="12">
                        <b-table :items="projects" :fields="fields" striped responsive="sm" sticky-header head-variant="dark">
                            <template #cell(show_details)="row">
                                <b-button size="sm" @click="row.toggleDetails" class="mr-2">
                                {{ row.detailsShowing ? 'Hide' : 'Show'}} Details
                                </b-button>

                                <!-- As `row.showDetails` is one-way, we call the toggleDetails function on @change -->
                                <b-form-checkbox v-model="row.detailsShowing" @change="row.toggleDetails">
                                Details via check
                                </b-form-checkbox>
                            </template>

                            <template #row-details="row">
                                <b-card>
                                    <b-row class="mb-2">
                                        <b-col sm="3" class="text-sm-right"><b>Name:</b></b-col>
                                        <b-col>{{ row.item.name }}</b-col>
                                    </b-row>

                                    <b-row class="mb-2">
                                        <b-col sm="3" class="text-sm-right"><b>Description:</b></b-col>
                                        <b-col>{{ row.item.description }}</b-col>
                                    </b-row>

                                    <b-row class="mb-2">
                                        <b-col sm="3" class="text-sm-right"><b>Active:</b></b-col>
                                        <b-col>{{ row.item.active }}</b-col>
                                    </b-row>

                                    <b-row class="mb-2">
                                        <b-col sm="3" class="text-sm-right"><b>Created At:</b></b-col>
                                        <b-col>{{ row.item.created_at }}</b-col>
                                    </b-row>

                                    <b-row class="mb-2">
                                        <b-col sm="3" class="text-sm-right"><b>Update At:</b></b-col>
                                        <b-col>{{ row.item.updated_at }}</b-col>
                                    </b-row>

                                    <b-button @click="onEdit(row.item.project_id,row.item.name,row.item.description,row.item.active)" size="sm" variant="warning">
                                        <b-icon icon="pencil-fill"></b-icon> &nbsp;Edit
                                    </b-button>
                                    <b-button @click="onDelete(row.item.project_id)" size="sm" variant="danger">
                                        <b-icon icon="trash-fill"></b-icon> &nbsp;Delete
                                    </b-button>
                                    <b-button size="sm" @click="row.toggleDetails">Hide Details</b-button>
                                </b-card>
                            </template>
                        </b-table>
                    </b-col>
                </div>
            </div>

            <b-modal id="modal-create" scrollable title="Add Project" hide-footer>
                <b-form @submit="onSubmit" @reset="onReset">
                    <b-form-group
                        id="input-group-1"
                        label="Project Name:"
                        label-for="input-1"
                    >
                        <b-form-input
                            id="input-1"
                            v-model="form.name"
                            placeholder="Enter project name"
                            required
                        ></b-form-input>
                    </b-form-group>

                    <b-form-group
                        id="input-group-2"
                        label="Description:"
                        label-for="input-2"
                    >
                        <b-form-input
                            id="input-2"
                            v-model="form.description"
                            placeholder="Enter description"
                            required
                        ></b-form-input>
                    </b-form-group>

                    <b-button type="submit" variant="success">Submit</b-button>
                    <b-button type="reset" variant="danger">Reset</b-button>
                </b-form>
            </b-modal>

            <b-modal id="modal-edit" scrollable title="Edit Project" hide-footer>
                <b-form @submit="onSubmitEdit" @reset="onReset">
                    <b-form-group
                        id="input-group-1"
                        label="Project Name:"
                        label-for="input-1"
                    >
                        <b-form-input
                            id="input-1"
                            v-model="form.name"
                            placeholder="Enter project name"
                            required
                        ></b-form-input>
                    </b-form-group>

                    <b-form-group
                        id="input-group-2"
                        label="Description:"
                        label-for="input-2"
                    >
                        <b-form-input
                            id="input-2"
                            v-model="form.description"
                            placeholder="Enter description"
                            required
                        ></b-form-input>
                    </b-form-group>

                    <b-form-group label="Active:" v-slot="{ ariaDescribedby }">
                        <b-form-radio v-model="form.active" :aria-describedby="ariaDescribedby" name="active" value="Y">Yes</b-form-radio>
                        <b-form-radio v-model="form.active" :aria-describedby="ariaDescribedby" name="active" value="N">No</b-form-radio>
                    </b-form-group>

                    <b-button type="submit" variant="success">Submit</b-button>
                    <b-button type="reset" variant="danger">Reset</b-button>
                </b-form>
            </b-modal>

            <b-modal id="modal-delete" scrollable title="Delete Project" hide-footer>
                <b-form @submit="onConfirmation">
                    
                    <b-row>
                        <b-col>Are you sure delete ?</b-col>
                    </b-row>
                    

                    <b-button type="submit" variant="danger">Delete</b-button>
                </b-form>
            </b-modal>
        </div>
    </div>
</template>

<script>
    import Navbar from "@/components/Navbar.vue";
    import axios from 'axios';
    
    axios.defaults.withCredentials = true;
    axios.defaults.headers.common = {
        'X-Requested-With': 'XMLHttpRequest',
        'Accept': 'application/json'
    };

    export default {
        name: "Project",
        components: {
            Navbar,
        },
        data() {
            return {
                form: {
                    name: '',
                    description: '',
                    active: '',
                },
                project_id: '',
                loggedIn: localStorage.getItem('loggedIn'),
                token: localStorage.getItem('token'),
                fields: ['project_id', 'name', 'show_details'],
                projects: [],
                loginFailed: null,
                message: ''
            }
        },
        mounted: function () {
            if (!this.loggedIn) {
                return this.$router.push({ name: 'Home' })
            }
        },
        created() {
            this.getProject();
        },
        methods: {
            getProject: function(){
                let currentObj = this;
                let config = {
                    headers: {
                        'Authorization': 'Bearer ' + currentObj.token
                    },
                }
                axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                .then(response => {
                    //debug cookie
                    console.log(response)

                    axios.get("https://api.oxion.tecorp.me/api/project", config)
                    .then(function(response){
                        //console.log(response.data);
                        if(response == 'Unauthenticated'){

                            localStorage.removeItem("loggedIn")    
                            localStorage.removeItem('token')
                            return this.$router.push({ name: 'Home' })

                        } else if (response.data.error) {
                            currentObj.message = response.data.message;
                            //remove localStorage
                            localStorage.removeItem("loggedIn")    
                            localStorage.removeItem('token')
                            return this.$router.push({ name: 'Home' })
                        }else{
                            currentObj.projects = response.data.data;
                        }
                    }).catch(error => {
                        console.log(error)
                        localStorage.removeItem("loggedIn")    
                        localStorage.removeItem('token')
                        return this.$router.push({ name: 'Login' })
                    })
                })
            },
            onSubmit(event) {
                event.preventDefault()
                //alert(JSON.stringify(this.form))

                if (this.form.name && this.form.description) {
                    axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                    .then(response => {

                        //debug cookie
                        console.log(response)

                        axios.post('https://api.oxion.tecorp.me/api/project', {
                            name: this.form.name,
                            description: this.form.description,
                        }).then(res => {

                            //debug user register
                            console.log(res)

                            if (res.data.error) {
                                this.message = res.data.message
                                this.$bvModal.hide('modal-create')
                            } else {
                                this.message = res.data.message

                                let currentObj = this;
                                let config = {
                                    headers: {
                                        'Authorization': 'Bearer ' + currentObj.token
                                    },
                                }
                                axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                                .then(response => {
                                    //debug cookie
                                    console.log(response)

                                    axios.get("https://api.oxion.tecorp.me/api/project", config)
                                    .then(function(response){
                                        //console.log(response.data);
                                        if(response == 'Unauthenticated'){

                                            localStorage.removeItem("loggedIn")    
                                            localStorage.removeItem('token')
                                            return this.$router.push({ name: 'Home' })

                                        } else if (response.data.error) {
                                            currentObj.message = response.data.message;
                                            //remove localStorage
                                            localStorage.removeItem("loggedIn")    
                                            localStorage.removeItem('token')
                                            return this.$router.push({ name: 'Home' })
                                        }else{
                                            currentObj.projects = response.data.data;
                                        }
                                    }).catch(error => {
                                        localStorage.removeItem("loggedIn")    
                                        localStorage.removeItem('token')
                                        return this.$router.push({ name: 'Login' })
                                    })
                                })

                                this.$bvModal.hide('modal-create')
                            }

                        }).catch(error => {
                            this.message = error
                            this.$bvModal.hide('modal-create')
                        })

                    })
                }

            },
            onReset(event) {
                event.preventDefault()
                // Reset our form values
                this.form.name = ''
                this.form.description = ''
                this.form.active = ''
                
                // Trick to reset/clear native browser form validation state
                this.show = false
                this.$nextTick(() => {
                this.show = true
                })
            },
            onEdit(project_id,name,description,active) {
                this.project_id = project_id
                this.form.name = name
                this.form.description = description
                this.form.active = active

                this.$bvModal.show('modal-edit')
            },
            onSubmitEdit(event) {
                event.preventDefault()
                //alert(JSON.stringify(this.form))

                if (this.form.name && this.form.description) {
                    axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                    .then(response => {

                        //debug cookie
                        console.log(response)

                        axios.put('https://api.oxion.tecorp.me/api/project/'+this.project_id, {
                            name: this.form.name,
                            description: this.form.description,
                            active: this.form.active,
                        }).then(res => {

                            //debug user register
                            console.log(res)

                            if (res.data.error) {
                                this.message = res.data.message
                                this.$bvModal.hide('modal-edit')
                            } else {
                                this.message = res.data.message

                                let currentObj = this;
                                let config = {
                                    headers: {
                                        'Authorization': 'Bearer ' + currentObj.token
                                    },
                                }
                                axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                                .then(response => {
                                    //debug cookie
                                    console.log(response)

                                    axios.get("https://api.oxion.tecorp.me/api/project", config)
                                    .then(function(response){
                                        //console.log(response.data);
                                        if(response == 'Unauthenticated'){

                                            localStorage.removeItem("loggedIn")    
                                            localStorage.removeItem('token')
                                            return this.$router.push({ name: 'Home' })

                                        } else if (response.data.error) {
                                            currentObj.message = response.data.message;
                                            //remove localStorage
                                            localStorage.removeItem("loggedIn")    
                                            localStorage.removeItem('token')
                                            return this.$router.push({ name: 'Home' })
                                        }else{
                                            currentObj.projects = response.data.data;
                                        }
                                    }).catch(error => {
                                        localStorage.removeItem("loggedIn")    
                                        localStorage.removeItem('token')
                                        return this.$router.push({ name: 'Login' })
                                    })
                                })

                                this.$bvModal.hide('modal-edit')
                            }

                        }).catch(error => {
                            this.message = error
                            this.$bvModal.hide('modal-edit')
                        })

                    })
                }

            },
            onDelete(project_id) {
                this.project_id = project_id

                this.$bvModal.show('modal-delete')
            },
            onConfirmation(event) {
                event.preventDefault()

                axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                .then(response => {

                    //debug cookie
                    console.log(response)

                    axios.delete('https://api.oxion.tecorp.me/api/project/'+this.project_id, {
                    }).then(res => {

                        //debug user register
                        console.log(res)

                        if (res.data.error) {
                            this.message = res.data.message
                            this.$bvModal.hide('modal-delete')
                        } else {
                            this.message = res.data.message

                            let currentObj = this;
                            let config = {
                                headers: {
                                    'Authorization': 'Bearer ' + currentObj.token
                                },
                            }
                            axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                            .then(response => {
                                //debug cookie
                                console.log(response)

                                axios.get("https://api.oxion.tecorp.me/api/project", config)
                                .then(function(response){
                                    //console.log(response.data);
                                    if(response == 'Unauthenticated'){

                                        localStorage.removeItem("loggedIn")    
                                        localStorage.removeItem('token')
                                        return this.$router.push({ name: 'Home' })

                                    } else if (response.data.error) {
                                        currentObj.message = response.data.message;
                                        //remove localStorage
                                        localStorage.removeItem("loggedIn")    
                                        localStorage.removeItem('token')
                                        return this.$router.push({ name: 'Home' })
                                    }else{
                                        currentObj.projects = response.data.data;
                                    }
                                }).catch(error => {
                                    localStorage.removeItem("loggedIn")    
                                    localStorage.removeItem('token')
                                    return this.$router.push({ name: 'Login' })
                                })
                            })

                            this.$bvModal.hide('modal-delete')
                        }

                    }).catch(error => {
                        this.message = error
                        this.$bvModal.hide('modal-delete')
                    })

                })

            }
        }
    }
</script>



