<template>
    <div>
        <div class="home" style="background-color: rgba(0, 0, 0, 0.03);">
            <Navbar />
            <div class="container">
                <b-alert v-if="loginFailed" variant="danger" show>{{ message }}</b-alert>
                <b-row class="justify-content-md-center">
                    <b-col cols="12" md="6">
                        <b-card class="mt-3">
                            <b-card-body>
                                <b-form @submit="onSubmit" @reset="onReset" v-if="show">
                                    <h3 class="text-center">Login</h3>
                                    <b-form-group
                                        id="input-group-1"
                                        label="Email address:"
                                        label-for="input-1"
                                    >
                                        <b-form-input
                                            id="input-1"
                                            v-model="form.email"
                                            type="email"
                                            placeholder="Enter email"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-form-group 
                                        id="input-group-2" 
                                        label="Password" 
                                        label-for="input-2"
                                    >
                                        <b-form-input
                                            id="input-2"
                                            type="password"
                                            v-model="form.password"
                                            placeholder="Enter password"
                                            required
                                        ></b-form-input>
                                    </b-form-group>

                                    <b-button type="submit" variant="success">Login</b-button>
                                    <b-button type="reset" variant="danger">Reset</b-button>
                                </b-form>
                            </b-card-body>
                        </b-card>
                    </b-col>
                </b-row>
                
                <b-card class="mt-3" header="Form Data Result">
                <pre class="m-0">{{ form }}</pre>
                </b-card>
            </div>
        </div>
    </div>
</template>

<script>
    import Navbar from "@/components/Navbar.vue";
    import axios from 'axios';

    export default {
        name: "Login",
        components: {
            Navbar,
        },
        data() {
            return {
                form: {
                    email: '',
                    password: '',
                },
                message: '',
                
                show: true,
                //state loggedIn with localStorage
                loggedIn: localStorage.getItem('loggedIn'),
                //state token
                token: localStorage.getItem('token'),
                //state user
                user: [],
                //state validation
                validation: [],
                //state login failed
                loginFailed: null
            }
        },
        methods: {
            onSubmit(event) {
                event.preventDefault()
                //alert(JSON.stringify(this.form))

                if (this.form.email && this.form.password) {
                    axios.get('https://api.oxion.tecorp.me/sanctum/csrf-cookie')
                    .then(response => {

                        //debug cookie
                        console.log(response)

                        axios.post('https://api.oxion.tecorp.me/api/login', {
                            email: this.form.email,
                            password: this.form.password
                        }).then(res => {

                            //debug user login
                            console.log(res)

                            if (res.data.error) {

                                //set state login failed
                                this.loginFailed = true
                                this.message = res.data.message

                            } else {

                                //set localStorage
                                localStorage.setItem("loggedIn", "true")

                                //set localStorage Token
                                localStorage.setItem("token", res.data.access_token)

                                //change state
                                this.loggedIn = true

                                this.message = res.data.message

                                //redirect profile
                                return this.$router.push({ name: 'Profile' })

                            }

                        }).catch(error => {
                            //set state login failed
                            this.loginFailed = true
                            this.message = error.message
                        })

                    })
                }

                this.validation = []

                if (!this.form.email) {
                    this.validation.email = true
                }

                if (!this.form.password) {
                    this.validation.password = true
                }

            },
            onReset(event) {
                event.preventDefault()
                // Reset our form values
                this.form.email = ''
                this.form.password = ''
                
                // Trick to reset/clear native browser form validation state
                this.show = false
                this.$nextTick(() => {
                this.show = true
                })
            }
        },
        //check user already logged in
        mounted() {
            if (this.loggedIn) {
                console.log(this.loggedIn)
                return this.$router.push({ name: 'Profile' })
            }
        }
    }
</script>


